.radarchart {
  width: 100%;
  height: 263px;
  min-width: 262px;
  background: #000;
  border-radius: 10px;
  &:hover {
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.3);
  }
}
@media screen and (max-width: 1330px) {
  .radarchart {
    height: 230px;
  }
}
@media screen and (max-width: 1024px) {
  .radarchart {
    height: 210px;
  }
}

.recharts-layer .recharts-polar-angle-axis {
  position: relative;
  left: -1px;
}
